import Vue from 'vue'
import Vuex from 'vuex'
import { Toast } from 'mint-ui'
import {
  makeHtmlContent,
  mergeJsonObject,
  wxShareConfig,
  makeFileUrl
} from "./utils";


import common from './components/module'
import article from './pages/article/index/module'
import comment from './pages/comment/index/module'
import community from './pages/community/index/module'
import message from './pages/message/index/module'
import service from './components/service'
import group from './pages/group/module'


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    UA: "",
    IS_APP: false,
    IS_NEW_USER: false,
    IS_DEV: false,
    IMG_INDEX: 0,
    CONTENT_IMGS: [],
    content: '',
    exist: false,
    extension_text: '',
    alert_stat: false,
    is_follow: false,
    get_login_type: false,
    visibleLogin: '',
    h5Cookies: '',
    isLogin: false,
    user: {},
    h5Adid: '',
    extension_text: '', // 
    get_login_type: '', // toFocus  toDown  inviter ,
    visibleLogin: false,
    authSuccess: false,
    res: {},
    shareLink: "",
    preShow: false,
    preImgs: [],
    preIndex: 0,
    wxConfig: {

    },
    shareConfig: {
      title: "",
      desc: "",
      link: "",
      imgUrl: ""
    }
  },
  mutations: {
    // 
    SET_CONTENT(state, para) {
      state.content = para
    },
    // 
    GET_EXIST_STATUS(state, para) {
      state.exist = para
    },
    // 
    SET_RES(state, para) {
      state.res = para
    },
    // 
    SET_EXTENSION_TEXT(state, para) {
      state.extension_text = para
    },
    // alert
    SHOW_ALERT(state, para) {
      state.alert_stat = para
    },
    // 
    SET_FOCUS_STAT(state, para) {
      state.is_follow = para
    },
    // 
    GET_LOGIN_TYPE(state, para) {
      state.get_login_type = para
    },
    // feed
    SET_VISIBLE_LOGIN(state, para) {
      state.visibleLogin = para
    },
    // 
    GET_VERSION(state) {
      let nvg = navigator.userAgent.toLowerCase(),
        nvgtype, nvgversion, nvgTypeToPowerCase;
      // window.navigator.appVersion 
      if (nvg.indexOf('android') > -1 || nvg.indexOf('adr') > -1 || nvg.indexOf('linux') > -1) {
        // android
        nvgtype = 'android';
        nvgTypeToPowerCase = 'Android';
        // android
        if (!!nvg.match(new RegExp("android\\s(\\d+(?:\\.\\d*)+)"))) {
          let v = nvg.match(new RegExp("android\\s(\\d+(?:\\.\\d*)+)"))
          nvgversion = v[1].replace(/\./g, "_")
        }
      } else if (nvg.indexOf('iphone') > -1 || nvg.indexOf('ipad') > -1 || nvg.indexOf('safari') > -1) {
        // ios
        nvgtype = 'ios'
        nvgTypeToPowerCase = 'IOS'
          // ios new RegExp("version/(\\d+(?:\\.\\d*)?)") // 
          // new RegExp("version/(\\d+(?:\\.\\d*)+)") 
        if (!!nvg.match(new RegExp("version/(\\d+(?:\\.\\d*)+)"))) {
          let v = nvg.match(new RegExp("version/(\\d+(?:\\.\\d*)+)"))
          nvgversion = v[1].replace(/\./g, "_")
        }
      } else {
        nvgtype = 'windows'
        nvgTypeToPowerCase = 'Windows'
      }
      state.nvgversion = nvgversion;
      state.nvgtype = nvgtype;
      state.nvgTypeToPowerCase = nvgTypeToPowerCase
    },
    // h5cookies
    SET_H5COOKIES(state, para) {
      console.log("h5", para)
      state.h5Cookies = para
    },
    // enter page time 
    SET_ENTER_TIME(state, para) {
      state.enter_time = para
    },
    // 
    SET_USER(state, para) {
      state.user = para
      state.isLogin = true;
    },
    setAuthStatus(state) {
      state.authSuccess = true
    },
    SET_WX_CONFIG(state, para) {
      state.wxConfig = para
    }
  },
  modules: {
    common,
    article,
    comment,
    community,
    message,
    group
  },
  actions: {
    // h5cookies
    async get_adcookie({
      commit,
      state
    }, {
      webUdid
    }) {
      let self = this
      try {
        let para = {
          webUdid: webUdid
        }
        let data = await service.get_adcookie(para)
        if (data.code === 0) {
          commit('SET_H5COOKIES', data.result.udid)
          return data.result.udid
        }
      } catch (e) {
        console.log('e==', e)
      }
    },
    // get_adcookies
    async down_adcookies({
      state,
      rootState,
      commit
    }) {
      let self = this,
        para = {
          webUdid: false,
          deviceType: rootState.nvgtype || 'ios',
          deviceVersion: rootState.nvgversion || '11_0_0',
          adid: Cookies.get('h5Adid') || 'closer-t1'
        }
      let data = await service.get_adcookie(para)
      return true
    },
    // 
    // h5    
    async down_statistics({
      state,
      commit
    }, {
      p1
    }) {
      var userId, h5cookie;
      if (Cookies.get("user")) {
        userId = JSON.parse(Cookies.get("user")).objectID;
      } else if (state.auth) {
        userId = state.auth.objectID;
      } else {
        userId = null
      }
      if (state.h5Cookies) {
        h5cookie = state.h5Cookies
      } else {
        h5cookie = null
      }
      let self = this,
        p2 = {
          userId: userId, // 'IDuserId>deviceId>cookieuserIddeviceIdcookie'
          deviceId: null, //	'IDiOSIDFAIDFVIMEImac userIddeviceIdcookie'
          cookie: h5cookie, //	'cookieH5userIddeviceIdcookie'
          platform: "H5", //	',:Android IOS H5'
          attachPlatform: state.nvgTypeToPowerCase || null, //	'H5platformH5AndroidIOSIOS'
          communityId: state.res.communityid || null, //		'id,'
          title: state.res.title || null, //		' '
          dreason: null, //		'actionfeedback["1", "2"]'
          time: Date.now(), //		''
          cost: Date.now() - state.enter_time || 0, //		'/'
          totalTime: state.duration_time || 0, //		'
        }
      let para = await mergeJsonObject(p1, p2);
      let data = await service.common(para);
      if (data.code === 0) {
        return true
      }
    },
    async wx_config({ state, commit }, payload) {
      if (ENV.wx) {
        console.log("wx_config")
        let params = {
          url: location.href
        };
        let { data } = await service.wechatConfig(params).catch(err => {
          Toast('')
          return;
        })
        if (typeof(data.code) != "undefined" && data.code == 0) {
          commit('SET_WX_CONFIG', data.result);
          let wxConfig = data.result;
          if (JSON.stringify(state.res) == "{}" && JSON.stringify(state.group.group) == "{}") {
            console.info("not index")
            if (Cookies.get("shareConfig")) {
              wxShareConfig(wxConfig, JSON.parse(Cookies.get("shareConfig")))
            }
            return;
          }
          let title, imgUrl, desc;
          if (location.href.indexOf("/community") > -1) {
            // 
            title = state.res.name ?
              state.res.name :
              "";
            desc = state.res.description ?
              state.res.description :
              " ";
            imgUrl = state.res.slogo ?
              state.res.slogo :
              state.res.blogo;
          } else if (location.href.indexOf("/group") > -1) {
            console.log("state.res", JSON.stringify(state.group.group))
              // 
            if (
              state.group.group &&
              state.group.group.group_info.group
            ) {
              let group = state.group.group.group_info.group;
              title = group.name ? group.name : "";
              if (group.description) {
                let description;
                try {
                  description = JSON.parse(
                    state.group.group.group_info.group.description
                  );
                  desc = description[0].content ?
                    description[0].content :
                    " ";
                } catch (e) {
                  desc =
                    state.group.group.group_info.group.description;
                }
              } else {
                desc = " ";
              }
              imgUrl = makeFileUrl(group.avatar);
            }
          } else {
            let content = state.content;
            // 
            if (state.res.int_type === 0) {
              // 
              if (content.text) {
                title = content.text;
              } else {
                title = "";
              }
              if (content.images && content.images.length > 0) {
                let d = content.images.map(x => {
                  x = "[]";
                  return x;
                });
                desc = d.join(" ");
                imgUrl = makeFileUrl(content.images[0].link);
              } else {
                desc = "[]";
                imgUrl = "";
              }
            } else if (state.res.int_type === 1) {
              // 
              if (content.text) {
                title = content.text;
              } else {
                title = "";
              }
              if (content.videos && content.videos.length > 0) {
                let d = content.videos.map(x => {
                  x = "[]";
                  return x;
                });
                desc = d.join(" ");
                imgUrl = makeFileUrl(content.videos[0].imageUrl);
              } else {
                desc = "[]";
                imgUrl = "";
              }
            } else {
              // 
              if (state.res.title) {
                title = state.res.title;
              } else if (content.text) {
                title = content.text;
              } else {
                title = content.summary;
              }
              desc = content.summary ? content.summary : "";
              imgUrl = makeFileUrl(state.res.cover) ?
                makeFileUrl(state.res.cover) :
                makeFileUrl(state.res.bigcover);
            }
          }
          let shareConfig = {
            title,
            desc,
            imgUrl
          }
          Cookies.set("shareConfig", shareConfig)
          wxShareConfig(wxConfig, shareConfig)
        }
      } else {
        return;
      }
    }
  }

})