import axios from 'axios'
import baseUrl from '../config';
const axio = axios.create({
    baseURL: process.env.BASE_API, // nodebaseURL
    timeout: 15000, // 
    withCredentials: true // cookie
  })
  // http request  
axio.interceptors.request.use(
  config => {
    let reqUrl = baseUrl.server + config.url
    config.url = reqUrl;
    if (!ENV.app) {
      config.headers['Closer-Agent'] = 'Closer-H5';
    } else {
      if (ENV.app && ENV.ios) {
        config.headers['Closer-Agent'] = 'Closer-Ios';
      } else if (ENV.app && ENV.android) {
        config.headers['Closer-Agent'] = 'Closer-Android';
      }
    }
    if (Cookies.get("uid")) {
      config.headers['X-Udid'] = Cookies.get("uid");
    }
    if (Cookies.get("aid")) {
      config.headers['X-Adid'] = Cookies.get("aid");
    }
    if (Cookies.get("token") && config.url.indexOf("auth") == -1 && config.url.indexOf("account") == -1 || Cookies.get("token") && config.url.indexOf("closer_account.bind_phone") != -1) {
      config.headers.Authorization = Cookies.get("token");
    }
    console.log(config.url)
    return config;
  },
  err => {
    return Promise.reject(err).catch(err);
  });
// http response  
axio.interceptors.response.use(
  response => {
    return response;
  },
  (err) => {
    let Promise = require('promise-polyfill').default;
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          err.message = ''
          break

        case 401:
          err.message = ''
          router.push({ name: 'worldcupIndex' })
          break

        case 403:
          err.message = ''
          router.push({ name: 'worldcupIndex' })
          break

        case 404:
          err.message = `: ${err.response.config.url}`
          break

        case 408:
          err.message = ''
          break

        case 500:
          err.message = ''
          break

        case 501:
          err.message = ''
          break

        case 502:
          err.message = ''
          break

        case 503:
          err.message = ''
          break

        case 504:
          err.message = ''
          break

        case 505:
          err.message = 'HTTP'
          break

        default:
      }
    } else {
      err.message = ''
    }
    if (err && err.response && err.response.data && err.response.data.message) {
      console.warn(err.response.data.message)
    } else {
      console.warn(err.message)
    }
    return Promise.reject(err).catch(err)
  });
export default axio