import Vue from 'vue';
import Router from 'vue-router';


//
const Comment = () =>
  import ('@/pages/comment/index')

// 
const Article = () =>
  import ('@/pages/article/index/index')

// 
const Message = () =>
  import ('@/pages/message/index/index')

// 
const Community = () =>
  import ('@/pages/community/index/index')

// jssdk DEMO
const Jssdk = () =>
  import ('@/pages/jssdk/index')

const Group = () =>
  import ('@/pages/group/index')

Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [{
      path: '/article/:id',
      name: 'article',
      component: Article,
    }, {
      path: '/comment/:id',
      name: 'comment',
      component: Comment,
    }, {
      path: "/jssdk",
      name: 'jssdk',
      component: Jssdk,
    }, {
      path: "/message/:sid?/:id?",
      name: 'message',
      component: Message,
    },
    {
      path: "/group/:id",
      name: "group",
      component: Group
    },
    {
      path: "/community/:id",
      name: 'community',
      component: Community,
    }
  ]
})
router.beforeEach(({
  meta,
  path,
  query,
  name,
  params
}, from, next) => {
  let pathName = path.split('/').slice(1),
    {
      type,
      category
    } = query;
  // metatitle
  document.title = meta.title ? meta.title : '';
  // pathquery
  /**
   * @type 
   *  0 - 
   *  1 - 
   *  2 - 
   *    @category 
   *       0 - () 
   *       1 - () 
   *       2 - ()
   *       3 - ()
   *       5 - ()
   */
  switch (pathName[0]) {
    case 'feed':
      if (type == '2') {
        if (category == '3') {
          // 
          router.replace({
            path: path.replace("feed", "comment")
          })
        } else {
          router.replace({
            path: path.replace("feed", "article")
          })
        }
      } else {
        next();
      }
      break;
    default:
      next();
      break;
  }

})

export default router